import React, { useState } from "react";
import "./style.css";
import { Image, Modal } from "semantic-ui-react";
import SIList from "./ListItem";
const List = props => {
  const [showModal, setShow] = useState(false);
  const [url, setUrl] = useState("");
  const handleModalOpen = url => {
    setUrl(url);
    setShow(true);
  };
  return (
    <div>
      <Modal
        open={showModal}
        dimmer="blurring"
        basic
        onClose={() => setShow(false)}
        style={{ zIndex: 1000 }}
      >
        <Modal.Content image>
          <Image as="a" target="_blank" href={url} wrapped src={url}></Image>
        </Modal.Content>
      </Modal>

      {props.seduce.map(url => {
        return (
          <SIList
            key={url}
            url={url}
            header={"Dead or Alive 6"}
            meta={"Sensitive Content"}
            description={"Play This Game"}
            onClick={handleModalOpen}
          ></SIList>
        );
      })}
    </div>
  );
};

export default List;
