import React from "react";
import { Responsive } from "semantic-ui-react";
import ForComp from "./forComp/comp";
import FroTab from "./forTab/forTab";
import ForMob from "./forMobile/forMob";
const Adult = props => {
  return (
    <div>
      <Responsive {...Responsive.onlyComputer}>
        <ForComp></ForComp>
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        <ForMob></ForMob>
      </Responsive>
      <Responsive {...Responsive.onlyTablet}>
        <FroTab></FroTab>
      </Responsive>
    </div>
  );
};

export default Adult;
