import React, { useState } from "react";
import {
  Card,
  Image,
  Placeholder,
  PlaceholderHeader,
  PlaceholderLine
} from "semantic-ui-react";
const SItem = props => {
  const [loading, setLoading] = useState(true);
  return (
    <Card
      key={props.url}
      style={{
        boxShadow: "0px 10px 10px rgba(0,0,0,0.4)",
        width: "100%",
        overflow: "hidden",
        marginTop: 20,
        cursor: "pointer"
      }}
    >
      {loading ? (
        <Card.Content>
          <Placeholder>
            <Placeholder.Image rectangular></Placeholder.Image>
          </Placeholder>
        </Card.Content>
      ) : (
        ""
      )}
      <Image
        as="div"
        onLoad={() => {
          setLoading(false);
        }}
        className={loading ? "notShow" : "blurr"}
        wrapped
        ui={false}
        src={props.url}
        onClick={() => {
          props.onClick(props.url);
        }}
      ></Image>

      {loading ? (
        <Card.Content>
          <Placeholder>
            <PlaceholderHeader></PlaceholderHeader>
            <PlaceholderLine></PlaceholderLine>
            <PlaceholderLine></PlaceholderLine>
          </Placeholder>
        </Card.Content>
      ) : (
        <Card.Content>
          <Card.Header>{props.header}</Card.Header>
          <Card.Meta>{props.meta}</Card.Meta>
          <Card.Description>{props.description}</Card.Description>
        </Card.Content>
      )}
    </Card>
  );
};
export default SItem;
