import React from "react";
import Problem from "./Problem";
import { Result } from "antd";
import { connect } from "react-redux";

const RenderProblems = props => {
  return (
    <div>
      {props.problems.length !== 0 && !props.isLoading ? (
        props.problems.map(problem => {
          console.log(problem.Media);
          return (
            <Problem
              key={problem._id}
              id={problem._id}
              profilePhoto={props.profilePhoto}
              username={props.username}
              header={"College Problems"}
              image={!problem.Media ? "" : problem.Media.imageUrl}
              description={problem.Description}
              createdAt={problem.createdAt}
              UpVotes={problem.UpVotes}
              DownVotes={problem.DownVotes}
            ></Problem>
          );
        })
      ) : !props.isLoading ? (
        <Result
          status="success"
          title="Hurray !! No Problems that I Am Facing"
        ></Result>
      ) : (
        ""
      )}
    </div>
  );
};

const mapStateToProps = state => {
  return {
    profilePhoto: state.userReducer.profilePhoto,
    username: state.userReducer.username
  };
};
export default connect(mapStateToProps)(RenderProblems);
