import React, { useState } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { IconButton } from "@material-ui/core";
import { Button } from "semantic-ui-react";
import url from "../../../../strings/url";
import {
  ArrowUpward,
  ArrowDownward,
  SentimentDissatisfied,
  Delete
} from "@material-ui/icons";
import {
  Image,
  Card,
  Statistic,
  PlaceholderImage,
  Placeholder,
  Modal
} from "semantic-ui-react";
// import { grey } from "@material-ui/core/colors";

const fontStyle = {
  fontFamily: "poppins",
  fontSize: 12
};
const fontStyle1 = {
  fontFamily: "poppins",
  fontSize: 15
};

const Problem = props => {
  const deletePost = async () => {
    const headers = new Headers();
    const token = localStorage.getItem("AnonymityToken#123");
    headers.append("Authorization", token);
    headers.append("content-type", "application/json");
    const request = new Request(`${url}/user/post/${props.id}`, {
      method: "DELETE",
      headers
    });
    try {
      const response = await fetch(request);
      if (response.status === 200) {
        window.location.reload(true);
      }
      setError(true);
    } catch (e) {
      setError(true);
    }
  };
  const [isImageLoading, setImage] = useState(true);
  const [isError, setError] = useState(false);
  const [showImageModal, setModal] = useState(false);
  const [showDeleteModal, setDeleteModal] = useState(false);
  console.log(props.image + "From Here");
  return (
    <div>
      <Card style={{ width: "100%", maxWidth: 1000 }}>
        <Card.Content>
          <Image
            src={props.profilePhoto}
            floated="left"
            size="mini"
            rounded
          ></Image>
          <Card.Header style={{ color: "grey", fontFamily: "poppins" }}>
            {props.username}
          </Card.Header>
          <Card.Meta style={{ color: "grey", fontFamily: "poppins" }}>
            {"Posted " +
              moment(props.createdAt).fromNow() +
              " \n" +
              "On " +
              moment(props.createdAt).format("LL")}
          </Card.Meta>
        </Card.Content>
        {isImageLoading ? (
          <Placeholder>
            <PlaceholderImage rectangular></PlaceholderImage>
          </Placeholder>
        ) : (
          ""
        )}
        <div
          onClick={() => {
            setModal(true);
          }}
        >
          <Image
            src={props.image}
            style={
              isImageLoading || isError
                ? { width: 0, height: 0 }
                : { width: "100%" }
            }
            onLoad={() => {
              setImage(false);
            }}
            onError={() => {
              setImage(false);
              setError(true);
            }}
          ></Image>
        </div>
        <Card.Content>
          <SentimentDissatisfied></SentimentDissatisfied>
          <Card.Content>
            <Card.Description
              style={{
                fontWeight: "bold",
                fontFamily: "poppins",
                wordBreak: "break-all",
                hyphens: "auto"
              }}
            >
              {props.description}
            </Card.Description>
          </Card.Content>
        </Card.Content>

        <Card.Content>
          <Statistic>
            <Statistic.Label style={{ ...fontStyle, color: "rgba(0,170,0,1)" }}>
              Up Votes
            </Statistic.Label>
            <Statistic.Label style={fontStyle1}>
              {props.UpVotes}
            </Statistic.Label>
          </Statistic>
          <Statistic>
            <Statistic.Label style={{ ...fontStyle, color: "rgba(0,0,0,1)" }}>
              Down Votes
            </Statistic.Label>
            <Statistic.Label style={fontStyle1}>
              {props.DownVotes}
            </Statistic.Label>
          </Statistic>
        </Card.Content>
        <Card.Content extra>
          <IconButton>
            <ArrowUpward></ArrowUpward>
          </IconButton>
          <IconButton>
            <ArrowDownward></ArrowDownward>
          </IconButton>
          <IconButton
            onClick={() => {
              setDeleteModal(true);
            }}
            style={{ float: "right" }}
          >
            <Delete style={{ color: "red" }}></Delete>
          </IconButton>
        </Card.Content>
      </Card>
      <Modal
        open={showImageModal}
        basic
        onClose={() => {
          setModal(false);
        }}
        closeIcon
      >
        <Image src={props.image} centered></Image>
      </Modal>
      <Modal open={showDeleteModal}>
        <Modal.Header>Delete Your Account</Modal.Header>
        <Modal.Content>
          <p>Are you sure you want to delete This Post ??</p>
        </Modal.Content>
        <Modal.Actions>
          <Button
            onClick={() => {
              setDeleteModal(false);
            }}
            negative
          >
            No
          </Button>
          <Button
            positive
            onClick={() => {
              deletePost();
            }}
          >
            Yes
          </Button>
        </Modal.Actions>
      </Modal>
    </div>
  );
};

export default connect()(Problem);
