import React from "react";
import { Pagination } from "@material-ui/lab";
import { Grid, CircularProgress, Snackbar } from "@material-ui/core";
import SICards from "../RenderList/renderList";
import SI from "../../../strings/SIURL";
import url from "../../../strings/url";
const style = { display: "flex", justifyContent: "center" };

const offset = 3;

const ForComp = props => {
  const [pageNo, setPageNo] = React.useState(1);
  const [loading, setloading] = React.useState(false);
  const [photos, setPhotos] = React.useState([
    "https://steamuserimages-a.akamaihd.net/ugc/796491634681646952/3A122AFBCE1E923AC4715E14E37967B52329C489/",
    "https://steamuserimages-a.akamaihd.net/ugc/757096217596557813/3B6A1BB231DE09FBC6E6C5B63A45268E25A0378B/",
    "https://steamuserimages-a.akamaihd.net/ugc/788605361819530942/DD2163EBAE4CBC52393F958D726DAB351292DBE2/",
    "https://steamuserimages-a.akamaihd.net/ugc/803243228397713285/20C337BA1903086701C7BA4BE95C0A7B6F45253A/",
    "https://steamuserimages-a.akamaihd.net/ugc/796490743072747119/6AE6527CBB95A5CE6A7AF40F1EC9AA5153921AEC/",
    "https://steamuserimages-a.akamaihd.net/ugc/797619330457481573/3CE41738CFF72B8CCC015237494F1580F4DEC816/"
  ]);

  const [error, setError] = React.useState(false);
  const onPageChange = (e, page) => {
    setPageNo(page);
    setloading(true);
    const headers = new Headers();
    headers.append("content-type", "application/json");
    const request = new Request(
      `${url}/user/SI/${(page - 1) * offset + 1}/${offset}`,
      { method: "GET", headers }
    );
    fetch(request)
      .then(async res => {
        if (res.status === 200) {
          const photos = await res.json();
          setPhotos(photos);
          setTimeout(() => setloading(false), 1000);
          return;
        }
        setTimeout(() => setloading(false), 1000);
        setError(true);
      })
      .catch(e => {
        setTimeout(() => setloading(false), 1000);
        setError(true);
      });
  };
  return (
    <div>
      <Grid container>
        <Grid item xs={12} style={style}>
          <Snackbar
            anchorOrigin={{ horizontal: "center", vertical: "top" }}
            open={error}
            onClose={() => setError(false)}
            autoHideDuration={6000}
            message="Error Loading New Images"
          ></Snackbar>
          <div>
            {loading ? (
              <div style={{ display: "flex", justifyContent: "center" }}>
                <CircularProgress
                  style={{
                    color: "black",
                    height: 100,
                    width: 100,
                    marginTop: 100
                  }}
                ></CircularProgress>
              </div>
            ) : (
              <SICards seduce={photos}></SICards>
            )}

            <div
              style={{
                display: "flex",
                justifyContent: "center",
                margin: 100
              }}
            >
              <Pagination
                count={parseInt(SI.length / offset)}
                page={pageNo}
                onChange={onPageChange}
              ></Pagination>
            </div>
          </div>
        </Grid>
      </Grid>
    </div>
  );
};

export default ForComp;
